var _this;
import image from '@/common/image.js';
import { pathToBase64, base64ToPath } from '@/js_sdk/gsq-image-tools/image-tools/index.js';
var sourceType = [
	['camera'],
	['album'],
	['camera', 'album']
]
var sizeType = [
	['compressed'],
	['original'],
	['compressed', 'original']
]
export default {
	data() {
		return {
			// title: 'choose/previewImage',
			input_content:'',
			imageList: [],
			imageBase64List:[],
			
			topicId: '',
			
			sourceTypeIndex: 2,
			sourceType: ['拍照', '相册', '拍照或相册'],
			sizeTypeIndex: 2,
			sizeType: ['压缩', '原图', '压缩或原图'],
			countIndex: 8,
			count: [1, 2, 3, 4, 5, 6, 7, 8, 9],
			
			//侧滑返回start
			startX: 0, //点击屏幕起始位置
			movedX: 0, //横向移动的距离
			endX: 0, //接触屏幕后移开时的位置
			//end
		}
	},
	mounted() {
		_this= this;
	},
	onLoad(data){
		console.log(data)
		this.topicId = data.id;
	},
	onUnload() {
		this.imageList = [],
			this.sourceTypeIndex = 2,
			this.sourceType = ['拍照', '相册', '拍照或相册'],
			this.sizeTypeIndex = 2,
			this.sizeType = ['压缩', '原图', '压缩或原图'],
			this.countIndex = 8;
	},
	
	methods: {
		async publish(){
			if (this.isEmpty(this.input_content) && this.imageBase64List.length == 0) {
				uni.showModal({ content: '内容不能为空', showCancel: false, });
				return;
			}
			
			uni.showLoading({title:'发布中'});
			
			// var location = await this.getLocation();//位置信息,可删除,主要想记录一下异步转同步处理
			// var images = [];
			// for(var i = 0,len = this.imageList.length; i < len; i++){
			// 	var image_obj = {name:'image-'+i,uri:this.imageList[i]};
			// 	images.push(image_obj);
			// }
			
			var url = this.getSessionUrl("tb/tbpost/sendPost");
			uni.request({
				url: url,
				method: 'POST',
				data: {
					content: this.input_content,
					images: this.imageBase64List,
					topicId: this.topicId
				},
				success: res => {
					console.log(res);
					var msg = "发布成功";
					var icon = "success";
					if (res.data.code != 0){
						msg = res.data.msg;
						icon = "none";
					}else{
						// setTimeout(()=>{
						// 	uni.navigateBack({
						// 		delta:-1
						// 	})
						// },1000)
						uni.navigateBack({
							delta:1
						})
					}
					uni.hideLoading();
					uni.showToast({
						icon: icon,
						msg: msg
					});
					
				},
				fail: () => {
					uni.hideLoading();
						uni.showToast({
							icon:'none',
							title:"发布失败,请检查网络"
					})
				},
				complete: () => {}
			});
			
			// uni.uploadFile({//该上传仅为示例,可根据自己业务修改或封装,注意:统一上传可能会导致服务器压力过大
			// 	url: 'moment/moments', //仅为示例，非真实的接口地址
			// 	files:images,//有files时,会忽略filePath和name
			// 	filePath: '',
			// 	name: '',
			// 	formData: {//后台以post方式接收
			// 		'user_id':'1',//自己系统中的用户id
			// 		'text': this.input_content,//moment文字部分
			// 		'longitude':location.longitude,//经度
			// 		'latitude':location.latitude//纬度
			// 	},
			// 	success: (uploadFileRes) => {
			// 		uni.hideLoading();
			// 		uni.showToast({
			// 			icon:'success',
			// 			title:"发布成功"
			// 		})
			// 		uni.navigateBack({//可根据实际情况使用其他路由方式
			// 			delta:1
			// 		});
			// 	},
			// 	fail: (e) => {
			// 		console.log("e: " + JSON.stringify(e));
			// 		uni.hideLoading();
			// 		uni.showToast({
			// 			icon:'none',
			// 			title:"发布失败,请检查网络"
			// 		})
			// 	}
			// });
		},
		
		getLocation(){//h5中可能不支持,自己选择
			return new Promise((resolve, reject) => {
				uni.getLocation({
					type: 'wgs84',
					success: function (res) {
						resolve(res);
					},
					fail: (e) => {  
						reject(e);
					}
				});
			} )
		},
		
		close(e){
		    this.imageList.splice(e,1);
			console.log(this.imageList);
			this.imageBase64List.splice(e,1);
			console.log(this.imageBase64List);
		},
		chooseImage: async function() {
			if (this.imageList.length === 9) {
				let isContinue = await this.isFullImg();
				console.log("是否继续?", isContinue);
				if (!isContinue) {
					return;
				}
			}
			uni.chooseImage({
				sourceType: sourceType[this.sourceTypeIndex],
				sizeType: sizeType[this.sizeTypeIndex],
				count: this.imageList.length + this.count[this.countIndex] > 9 ? 9 - this.imageList.length : this.count[this.countIndex],
				success: (res) => {

					//






					
					//
					// this.imageList = this.imageList.concat(res.tempFilePaths)//非APP平台不支持自定义压缩,暂时没有处理,可通过uni-app上传组件的sizeType属性压缩
					//
					var image = res.tempFilePaths.shift();
					this.imageList = this.imageList.concat(image);
					// this.imageList.push(res.tempFilePaths.shift());
					
					console.log(this.imageList);
					
					// Promise.all(this.imageList.map(path => pathToBase64(path)))
					//   .then(res => {
					//     // [base64, base64...]
					// 	// this.imageBase64List = _this.imageBase64List.concat(res);
						
					// 	this.imageBase64List = res;
						
					// 	// this.imageBase64List.push(res[0]);
					// 	console.log(_this.imageBase64List);
					//   })
					//   .catch(error => {
					//     console.error(error)
					//   })
					console.log(image)
					pathToBase64(image)
					  .then(base64 => {
					    _this.imageBase64List = _this.imageBase64List.concat(base64);
						console.log(_this.imageBase64List)
					  })
					  .catch(error => {
					    console.error(error)
					  })
					
				}
			})
		},
		isFullImg: function() {
			return new Promise((res) => {
				uni.showModal({
					content: "已经有9张图片了,是否清空现有图片？",
					success: (e) => {
						if (e.confirm) {
							this.imageList = [];
							res(true);
						} else {
							res(false)
						}
					},
					fail: () => {
						res(false)
					}
				})
			})
		},
		previewImage: function(e) {
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.imageList
			})
		},
		touchStart: function(e) {
			this.startX = e.mp.changedTouches[0].pageX;
		},
		
		touchEnd: function(e) {
			this.endX = e.mp.changedTouches[0].pageX;
			if (this.endX - this.startX > 200) {
				uni.navigateBack();
			}
		}
	}
}
